
/* ====  Components/Jamborton/style.css  ==== */

.jumbotron {
  padding: 100px;
  background-image: url(/static/media/google_books.a5c14d4d.jpg);
  background-size: cover;
  color: white;
}


/* ====  Components/Card/style.css  ==== */

.card {
  background-color: rgb(12, 12, 12);
}

.card-header {
  color: rgb(182, 182, 182);
}

.card-body {
  color: rgb(199, 199, 199);
}


/* ====  Components/Form/style.css  ==== */

.form-control {
  color: #fff;
  border: none;
  background-color: rgb(48, 47, 47);
  padding-left: 10px;
}

.form-control:focus {
  color: #fff;
  background-color: rgb(48, 47, 47);
  padding-left: 10px;
}

.form-control::-webkit-input-placeholder {
  color: rgb(104, 104, 104);
}

.form-control::-moz-placeholder {
  color: rgb(104, 104, 104);
}

.form-control::-ms-input-placeholder {
  color: rgb(104, 104, 104);
}

.form-control::placeholder {
  color: rgb(104, 104, 104);
}

input, textarea {
  background-color:#666;
  color: #FFF;
}

.pull-right .btn {
 border: solid 1px rgb(255, 163, 5);
}





/* ====  Components/List/style.css  ==== */

.list-group-item {
  background-color: rgb(34, 34, 34);
  border: rgb(69, 68, 68) solid 1px;
  padding-top: 25px;
  padding-bottom: 25px;
}

.list-group-item p {
  font-size: 17px;
  color:rgb(201, 201, 201);
}


/* ====  Components/Book/style.css  ==== */

.btn-container {
  float: right;
}

.btn-container .btn {
  border: solid 1px rgb(5, 168, 255);
  font-weight: bold;
}

@media (max-width: 767px) {
  .btn-container {
    margin-bottom: 15px;
  }
}


/* ====  Components/Footer/style.css  ==== */

.pull-right {
  color: rgb(192, 138, 45);
  text-align:center;
}



/* ====  src/pages/style.css  ==== */

.transBox {
    background-color: rgba(0, 0, 0, 0.5);
    padding-top: 10px;
    padding-bottom: 7px;
    border-radius: 5px;
}

@media (min-width:1200px) and (max-width:5000px) {
    .transBox {
        width: 58%;
        margin-left: 21%;
    }
}

@media (min-width:992px) and (max-width:1199px) {
    .transBox {
        width: 70%;
        margin-left: 15%;
    }
}

@media (min-width:768px) and (max-width:991px) {
    .transBox {
        width: 100%;
    }
}

@media (min-width:507px) and (max-width:767px) {
    .transBox {
        width: 130%;
        margin-left: -15%;
    }

    .transBox .text-center {
        font-size: 19px;
    }
}

@media (min-width:441px) and (max-width:506px) {
    .transBox {
        width: 170%;
        margin-left: -35%;
    }

    .transBox .text-center {
        font-size: 21px;
    }
}

@media (min-width:341px) and (max-width:440px) {
    .transBox {
        width: 200%;
        margin-left: -50%;
    }

    .transBox .text-center {
        font-size: 21px;
    }
}

@media (min-width:321px) and (max-width:340px) {
    .transBox {
        width: 280%;
        margin-left: -90%;
    }

    .transBox .text-center {
        font-size: 19px;
    }
}

@media (min-width:300px) and (max-width:320px) {
    .transBox {
        width: 320%;
        margin-left: -110%;
    }

    .transBox .text-center {
        font-size: 19px;
    }
}

@media screen and (max-width:299px) {
    .transBox {
        width: 400%;
        margin-left: -150%;
    }

    .transBox .text-center {
        font-size: 17px;
    }
}

.transBox .text-center {
    color: rgb(255, 184, 60);
}

#save {
    border: solid 1px rgb(5, 255, 159);
    font-weight: bold;
}

#delete {
    border: solid 1px rgb(255, 30, 5);
    font-weight: bold;
}

/* ====  Components/Nav/style.css  ==== */

.navbar {
  border-radius: 0;
  background-color: rgb(0, 23, 52);
}
.navbar .navbar-brand {
  color: #fcb232;
}
.bookLogo {
  width: 41px;
  margin-right: 8px;
  margin-left: 25px;
}

.navbar-brand {
  font-size: 23px;
}

.nav-item {
  font-size: 19px;
}
